


















import {Component, Prop, Ref, Vue} from "vue-property-decorator";
import {removeHtmlTags} from "@/common";

@Component({})
export default class RealtyCard2 extends Vue {
  @Ref('balloon')
  refBalloon!: HTMLElement
  @Prop({ required: true })
  imgPath!: string
  @Prop({ required: true })
  name!: string
  @Prop({ required: true })
  area!: number
  @Prop({ required: true })
  description!: string
  @Prop({ required: true })
  price!: number
  @Prop({ required: true })
  id!: number
  @Prop({ required: false, default: false, type: Boolean }) disableLink!: boolean

  get descriptionValue (): string {
    return removeHtmlTags(this.description).slice(0, 165) + '...'
  }

}

